@charset "UTF-8";
body {
  background-color: var(--background-color-light); }

/*MOBILE*/
.section-1 {
  background-color: var(--background-color-light);
  text-align: var(--text-align-center);
  width: 100vw;
  font-family: "Poppins";
  color: #0c474b; }

.title2 {
  margin: 41px auto auto auto;
  width: 322px;
  grid-column-start: 1;
  grid-column-end: 1;
  font-size: 24px; }

.wrapper1 {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-columns: 100vw; }

.table {
  margin: 37px auto auto auto;
  width: 90px;
  height: 90px;
  grid-column-start: 1;
  grid-column-end: 1; }

.title-need {
  margin-top: 47px;
  grid-column-start: 1;
  grid-column-end: 1;
  font-size: 20px; }

.text-need {
  margin: 12px auto 18px auto;
  font-size: var(--font-size-);
  width: 208px;
  grid-column-start: 1;
  grid-column-end: 1;
  margin-bottom: 18px; }

.wrapper2 {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-columns: 100vw; }

.bag {
  margin: 40.5px auto auto auto;
  width: 90px;
  height: 90px;
  grid-column-start: 1;
  grid-column-end: 1; }

.text-materias {
  margin: 12px auto 18px auto;
  font-size: var(--font-size-);
  width: 208px;
  grid-column-start: 1;
  grid-column-end: 1;
  margin-bottom: 18px; }

.wrapper3 {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-columns: 100vw; }

.guide {
  margin: 40px auto auto auto;
  width: 90px;
  height: 90px;
  grid-column-start: 1;
  grid-column-end: 1; }

.title-guide {
  margin: 37.5px auto 0px auto;
  grid-column-start: 1;
  grid-column-end: 1;
  font-size: 20px; }

.text-guide {
  margin: 12px auto 18px auto;
  font-size: var(--font-size-);
  width: 208px;
  grid-column-start: 1;
  grid-column-end: 1;
  margin-bottom: 18px; }

